<template>
  <v-app>
    <v-navigation-drawer temporary absolute v-model="sideNav">
      <v-list>
        <v-list-tile
          v-for="item in menuItems"
          :key="item.title"
          :to="item.link">
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>{{ item.title }}</v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar dark class="primary">
      <v-toolbar-side-icon
        @click="sideNav = !sideNav"
        class="hidden-sm-and-up "></v-toolbar-side-icon>
      <v-toolbar-title>
        <router-link to="/" tag="span" class="craftlabs__link">craftlabs</router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-xs-only">
        <v-btn
          flat
          v-for="item in menuItems"
          :key="item.title"
          :to="item.link">
          <v-icon left dark>{{ item.icon }}</v-icon>
          {{ item.title }}
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <main>
      <router-view></router-view>
    </main>
  </v-app>
</template>

<script>
/* eslint-disable no-console,no-underscore-dangle */
import auth from './services/auth';

export default {
  async beforeCreate() {
    try {
      const user = await auth.getUser();
      if (user.data._id) {
        this.$store.dispatch('logUserIn', user.data);
      } else {
        this.$router.push({ name: 'Home' });
      }
    } catch (err) {
      console.error('error', err);
    }
  },
  data() {
    return {
      sideNav: false,
    };
  },
  computed: {
    menuItems() {
      let menuItems = [
        { icon: 'face', title: 'Register', link: '/register' },
        { icon: 'lock_open', title: 'Login', link: '/login' },
      ];
      if (this.userIsAuthenticated) {
        menuItems = [
          { icon: 'store', title: 'View Shop', link: '/shop' },
          { icon: 'person', title: 'Profile', link: '/profile' },
          { icon: 'lock', link: '/logout' },
        ];
      }
      return menuItems;
    },
    userIsAuthenticated() {
      return !!this.$store.getters.user;
    },
  },
};
</script>

<style>
  .craftlabs__link {
    cursor: pointer;
  }
</style>



